<template>
  <el-row v-loading.fullscreen.lock="loadingPage">
    <el-col :span="24">
      <el-row type="flex" class="row-bg" justify="space-between">
        <el-col :span="12">
          <h3>防护组件管理</h3>
        </el-col>
        <el-col :span="12" class="text-align-right">
          
        </el-col>
      </el-row>
      <div class="margin-4x"></div>
      <el-row>
        <el-col :span="24">
          <el-row class="text-align-right">
            <div class="ssl-search-input">
              <el-input placeholder="请输入名称进行搜索" prefix-icon="el-icon-search" v-model="dataSearch"></el-input>
            </div>

          </el-row>
          <div class="demo-block">
            <el-table
              :data="tableData.filter((data) => !dataSearch || data.ssl.toLowerCase().includes(dataSearch.toLowerCase()))"
              style="width: 100%">
              <el-table-column prop="name" label="组件名称"></el-table-column>
              <el-table-column prop="detail" label="组件详情"></el-table-column>
              <el-table-column label="关联网站">
                <template #default="scope">
                  <p>
                    网站：{{ scope.row.waf_domain_count }}
                  </p>
                  <p>
                    网站分组：{{ scope.row.waf_group_domain_count }}
                  </p>
                </template>
              </el-table-column>
              <el-table-column prop="demo_conf" label="默认配置"></el-table-column>

              <el-table-column label="操作" align="right">
                
                <template #default="scope">
                  <el-button size="mini" @click="handleEdit(scope.row)" class="button-block" type="text">编辑</el-button>

                  <el-popover placement="top" width="160" v-model:visible="scope.row.isVisiblePopover">
                    <p>确定删除吗？</p>
                    <div style="text-align: right; margin: 0">
                      <el-button size="mini" type="text" @click="scope.row.isVisiblePopover = false">取消</el-button>
                      <el-button type="primary" size="mini" @click="handleDelete(scope.row)" :loading="loading">确定
                      </el-button>
                    </div>
                    <template #reference>
                      <el-button type="text" size="mini" @click="scope.row.isVisiblePopover = true">删除</el-button>
                    </template>

                  </el-popover>
                  </template>
                </el-table-column>
              </el-table>
            </div>
          </el-col>
        </el-row>
      </el-col>

      <el-dialog v-model="dialogConfigFormVisible" title="配置" :close-on-click-modal="false" width="520px"
        @closed="dialogCloseConfig">
        <el-form class="form-download-rule-dialog" :model="configForm" label-position="left" label-width="130px"
          :rules="rules" ref="configForm">
        <el-form-item label="配置" key="1" >
          <el-input v-model="conf" :rows="10" type="textarea" placeholder="Please input" />
        </el-form-item>
      </el-form>
      <template #footer class="dialog-footer">
        <el-button @click="dialogConfigFormVisible = false">取消</el-button>
        <el-button type="primary" @click="onClickConfigSubmit('configForm')" :loading="loading">确定
        </el-button>
      </template>
    </el-dialog>
  </el-row>
</template>
<script>
import { mixin, JXAjax } from "../assets/scripts/common";
export default {
  mixins: [mixin],
  data() {
    return {
      dataSearch: "",
      loadingPage: false,
      loading: false,
      tableData: [],
      configForm: {},
      conf: {},
      dialogConfigFormVisible: false,
    };
  },
  computed: {
    rules() {
      return {
        
      }
    }
  },

  mounted() {
    this.getData();
  },
  methods: {
    getData() {
      var t = this;
      JXAjax(
        "get",
        "/waf/waf_get_sys_component_protection_list",
        {},
        function (response) {
          t.loadingPage = false;
          t.tableData = response.data.message;
          t.tableData.forEach(item => {
            item.isVisiblePopover = false;
          })
        },
        function () {
          t.loadingPage = false;
        }
      );
    },
    handleEdit(data) { 
      this.dialogConfigFormVisible = true;
      this.configForm = data;
      var jsonObj = JSON.parse(data.demo_conf) 
      this.conf = JSON.stringify(jsonObj, null, 4);
    },

    dialogCloseConfig() { 
      this.$refs["configForm"].resetFields();
    },
    onClickConfigSubmit(configForm) {
      var t = this;
      var o = "";
      var jsonObj = {};
      try  {
        o = JSON.stringify(JSON.parse(t.conf));
        jsonObj = JSON.parse(t.conf); 
        t.conf = JSON.stringify(jsonObj, null, 4);
      } catch(error) {
        t.$message({
          showClose: true,
          message: '请输入正确JSON格式',
          type: "error",
        });
        return false;
      }
      
      t.configForm.demo_conf = o;
      this.$refs[configForm].validate((valid) => {
        if (valid) {
          t.loading = true;
          JXAjax(
            "post",
            "/waf/waf_edit_sys_component_protection",
            t.configForm,
            function (response) {
              t.loading = false;
              t.dialogConfigFormVisible = false;
              t.getData();
            },
            function () {
              t.loading = false;
            }
          );
        }
      });
    },


    handleDelete(data) {
      var t = this;
      t.loading = true;
      JXAjax(
        "post",
        "/waf/waf_delete_sys_component_protection",
        { name: data.name, uuid: data.uuid },
        function (response) {
          data.isVisiblePopover = false;
          t.loading = false;
          t.getData();
        },
        function () {
          t.loading = false;
        }
      );
    },
  },
};
</script>
<style>
.el-button.button-block {
  display: block;
  margin-left: 0px;
  text-align: right;
  width: 100%;
}

.icon-success {
  color: #67c23a;
  margin-right: 5px;
}

.icon-error {
  color: #f56c6c;
  margin-right: 5px;
}

.icon-warning {
  color: #e6a23c;
  margin-right: 5px;
}

.ssl-setting {
  float: right;
}

.ssl-redirect {
  margin-top: 5px;
  margin-bottom: 0px !important;
}

.el-form-item.is-required .redirect-box .el-form-item__label:before {
  content: "";
}

.ssl-search-input {
  display: block;
  float: left;
  text-align: left;
}

.ssl-search-input .el-input {
  width: 100%;
}

.no-padding {
  padding: 0;
}

.node-detail span {
  font-size: 12px;
}
</style>
